<template>
    <section class="a-dialog">
        <div class="a-model">
            <div class="a-model-inner">
                <div class="a-model-tips">
                    <div class="a-model-tip-item" v-for="(item, index) in tips" :key="index">
                        <span v-html="item"></span>
                    </div>
                </div>
                <div class="a-btn J_copy-btn" data-clipboard-target="#clipboard-command" @click="onsubmit(price)">{{btntext}}</div>
            </div>
            <div class="a-model-close" @click="onclose"></div>
        </div>
    </section>
</template>

<style lang="less">
    @import "../assets/less/common.less";
    @closeBtnUrl: 'data:image/png;base64,';
    @ruleCloseUrl: 'data:image/png;base64,';
    .a {
        &-dialog {
            position: fixed;
            top: 0;
            left: 0;
            font-size: 24/@b;
            z-index: 10;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.6);
        }
        &-model {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            width: 640/@b;
            background: #fff;
            border-radius: 12/@b;
        }
        &-model-tips {
            box-sizing: border-box;
            padding-top: 36/@b;
            padding-left: 36/@b;
            border-bottom: 1px solid #ccc;
        }
        &-model-tip-item {
            margin-bottom: 28/@b;
            em {
                color: #ec5869;
            }
        }

        &-btn {
            height: 75/@b;
            line-height: 75/@b;
            text-align: center;
            background: #ec5869;
            border-radius: 0 0 12/@b 12/@b;
            color: #fff;
            font-size: 28/@b;
            font-weight: 600;
        }
        &-model-close {
            position: absolute;
            top: -15/@b;
            right: -15/@b;
            width: 40/@b;
            height: 40/@b;
            background: url(@closeBtnUrl) no-repeat;
            background-size: cover;
        }
    }
    
</style>

<script>
import Clipboard from 'clipboard';
export default {
    data: () => ({
        price: '',
    }),
    methods: {

    },
    mounted() {
        // 两种按钮时复制贝口令
        if (this.btntext === '我要打开APP' || this.btntext === '下载APP') {
            let copyStr = '复制';
            let clipboard = new Clipboard('.J_copy-btn');
            console.log('监听')
            clipboard.on('success', (e) => {
                console.log('复制成功')
                return undefined;
            });
            clipboard.on('error', () => {
                console.log('复制失败')
            });
        }
    },
    props: [
        'tips',
        'onsubmit',
        'onclose',
        'btntext'
    ]
}
</script>



