<template>
    <section class="r-dialog">
        <div class="r-model">
            <div class="close-btn" @click="onclose">
            </div>
            <div class="rule-title">
                活动规则
            </div>
            <div class="rule-list">
                <div class="rule-item" v-for="(item, index) in rules" :key="index">
                    {{item}}
                </div>
            </div>
        </div>
    </section>
</template>

<style lang="less">
    @import "../assets/less/common.less";
    @closeBtnUrl: 'data:image/png;base64,';
    @ruleCloseUrl: 'data:image/png;base64,';
    .r {
        &-dialog {
            position: fixed;
            top: 0;
            left: 0;
            z-index: 10;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.6);
            font-size: 24/@b;
        }
        &-model {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            width: 580/@b;
            padding-top: 12/@b;
            padding-bottom: 24/@b;
            background: #fff;
            border-radius: 12/@b;
            .close-btn {
                position: absolute;
                top: 12/@b;
                right: 12/@b;
                width: 40/@b;
                height: 40/@b;
                background: url(@ruleCloseUrl) no-repeat;
                background-size: cover;
            }
        }
    }
    .rule-title {
        color: #ec5869;
        font-size: 32/@b;
        height: 80/@b;
        line-height: 80/@b;
        text-align: center;
        font-weight: 600;
        border-bottom: 1px solid #ccc;
    }
    .rule-list {
        padding: 24/@b 36/@b;
    }
    .rule-item {
        margin-top: 16/@b;
        font-size: 24/@b;
        color: #333;
    }

</style>

<script>
export default {
    data: () => ({
        rules: [
            '1、每人初始五次猜价机会，分享好友助力后，可增加猜价机会；',
            '2、猜中价格的用户，有机会获得力士沐浴乳或红包，奖品随机赠送，先到先得；',
            '3、为防止作弊，每个用户竞猜的商品价格在一定区间范围之内随机生成；',
            '4、中奖用户，根据提示填写收货地址，活动结束后7个工作日会安排发货；',
            '5、最后告诉大家的一个秘诀是，多分享给朋友助力，然后靠运气和头脑竞猜价格成功，免费领取商品。祝大家玩得愉快！',
        ]
    }),
    methods: {

    },
    mounted() {
    },
    props: [
        'tips',
        'onsubmit',
        'onclose'
    ]
}
</script>



