<template>
    <section class="dialog">
        <div class="model">
            <div class="model-inner">
                <div class="model-tips">
                    <span v-html="tips"></span>
                </div>
                <div class="model-input-box">
                    <input class="model-input" placeholder="请填写你要猜的价格，最小单位分" type="text" v-model="price">
                </div>
                <div class="btn" @click="onsubmit(price)">{{inputing ? '正在提交...' : '确定'}}</div>
            </div>
            <div class="model-close" @click="onclose"></div>
        </div>
    </section>
</template>

<style lang="less">
    @import "../assets/less/common.less";
    @closeBtnUrl: 'data:image/png;base64,';
    @ruleCloseUrl: 'data:image/png;base64,';
    .dialog {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 10;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.6);
    }
    .model {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 600/@b;
        height: 272/@b;
        background: #fff;
        border-radius: 12/@b;
    }
    
    .model-tips {
        box-sizing: border-box;
        padding: 36/@b;
        height: 100/@b;
        border-bottom: 1px solid #ccc;
        em {
            color: #ec5869;
        }
    }
    .model-input-box {
        padding-left: 36/@b;
        height: 100/@b;
    }
    .model-input {
        display: inline-block;
        border: none;
        width: 100%;
        margin-top: 34/@b;
        height: 32/@b;
        font-size: 28/@b;
        line-height: 32/@b;
        color: #333;
        ::-webkit-input-placeholder {
            color: #ccc;
        }
    }
    .btn {
        height: 75/@b;
        line-height: 75/@b;
        text-align: center;
        background: #ec5869;
        border-radius: 0 0 12/@b 12/@b;
        color: #fff;
        font-size: 28/@b;
        font-weight: 600;
    }
    .model-close {
        position: absolute;
        top: -15/@b;
        right: -15/@b;
        width: 40/@b;
        height: 40/@b;
        background: url(@closeBtnUrl) no-repeat;
        background-size: cover;
    }
</style>

<script>
export default {
    data: () => ({
        price: '',
    }),
    methods: {

    },
    mounted() {

    },
    props: [
        'tips',
        'onsubmit',
        'onclose',
        'inputing'
    ]
}
</script>



